
*{box-sizing:border-box
}
html{font-family:"Segoe UI", 'Microsoft Yahei', Helvetica, Arial
}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,button,textarea,p,blockquote,th,td{margin:0;padding:0
}
h1,h2,h3,h4,h5,h6{font-weight:500;line-height:1.426
}
article h1,article h2,article h3,article h4,article h5,article h6{margin:10px 0 15px 0
}
article h1,section h1{font-size:2em
}
a,a:hover,a:active,a:focus,input,input:hover,input:active,input:focus,select,select:hover,select:active,select:focus,textarea,textarea:hover,textarea:active,textarea:focus{outline:none
}
button,button:active,button:focus,button:hover{outline:none
}
ul,ol{list-style:none
}
body{color:#345
}
a{color:#3498db
}
blockquote{padding-left:20px;margin:.5em 0;position:relative;border-left:4px solid #3498db
}
code{padding:10px 20px;display:inline-block;background-color:#324057;color:#fffffb;font-family:Consolas, Monaco, Droid, Sans, Mono, Source, Code, Pro, Menlo, Lucida, Sans, Type, Writer, Ubuntu, Mono;border-radius:5px
}
small{color:#7f8c8d
}
hr{margin:20px 0
}
button,[role='button']{padding:9px 15px;display:inline-block;border:none;background-color:#7f8c8d;border-radius:5px;color:#fffffb;transition:background-color .2s
}
button[disabled],[role='button'][disabled]{opacity:.35;cursor:not-allowed
}
button:hover,[role='button']:hover{background-color:#95a5a6
}
button:active,[role='button']:active{box-shadow:inset 0 1px 2px 0 rgba(0,0,0,0.56)
}
label{font-weight:bold;display:inline-block;padding:0 5px
}
input,select,textarea{padding:8px;border-radius:5px;border:1px solid #95a5a6;background-color:#fffffb;width:220px;font-family:"Segoe UI", 'Microsoft Yahei', Helvetica, Arial
}
input::-moz-placeholder,select::-moz-placeholder,textarea::-moz-placeholder{color:#95a5a6;font-size:inherit
}
input:-ms-input-placeholder,select:-ms-input-placeholder,textarea:-ms-input-placeholder{color:#95a5a6;font-size:inherit
}
input::-webkit-input-placeholder,select::-webkit-input-placeholder,textarea::-webkit-input-placeholder{color:#95a5a6;font-size:inherit
}
input:-webkit-autofill,select:-webkit-autofill,textarea:-webkit-autofill{-webkit-box-shadow:0 0 0px 1000px #fffffb inset
}
input:focus,select:focus,textarea:focus{border-color:#3498db
}
textarea{width:300px;resize:none
}
input[type="radio"],input[type="checkbox"]{width:20px
}
table{background-color:#fffffb;border-collapse:collapse;font-family:"Segoe UI", 'Microsoft Yahei', Helvetica, Arial;box-shadow:0 0 5px 1px rgba(0,0,0,0.26);overflow:hidden
}
table tr{text-align:left
}
table th{color:#fffffb;background-color:#95a5a6
}
table th,table td{padding:12px
